































import { defineComponent, computed, PropType } from '@vue/composition-api'

// Utitlies
import { xkey } from '@/models/otelattr'
import { AttrMap } from '@/models/span'

export default defineComponent({
  name: 'AttrTable',

  props: {
    attrs: {
      type: Object as PropType<AttrMap>,
      required: true,
    },
  },

  setup(props) {
    const attrKeys = computed((): string[] => {
      const keys = Object.keys(props.attrs).filter((key) => {
        switch (key) {
          case xkey.spanId:
          case xkey.spanTraceId:
          case xkey.spanGroupId:
          case xkey.spanSystem:
          case xkey.spanName:
          case xkey.spanKind:
          case xkey.spanTime:
          case xkey.spanDuration:
          case xkey.spanStatusCode:
            return false
          default:
            return true
        }
      })
      keys.sort()
      return keys
    })

    return {
      xkey,
      attrKeys,
    }
  },
})
